body {
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-smoothing: antialiased;
  text-rendering: auto;
  font-size: 14px;
  font-family: "arial, sans-serif";
  color: #333;
  background-color: #fff;
}

a {
  color: #333;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

body {
  margin: 0;
  padding: 0;
  color: #000;
}

canvas {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: block;
}

.caption {
  position: absolute;
  top: 40px;
  left: 38px;
  padding-left: 42px;
  font-family: 'Archivo Black', sans-serif;
}

.caption:before {
  position: absolute;
  top: 9px;
  left: 0;
  bottom: 0;
  content: ' ';
  display: block;
  width: 6px;
  height: 114px;
  margin: auto;
  background: #000;
  margin-right: 0px;
}

.caption h1 {
  margin: 0;
  font-size: 60px;
}

.caption h1 span {
  letter-spacing: -0.15em;
}

.caption time {
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  font-size: 18px;
  letter-spacing: 0.05em;
  margin-top: 10px;
  display: block;
}

.caption ul {
  margin: 8px 0 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  display: flex;
}

.caption ul > li {
  font-weight: bold;
  font-size: 12px;
  letter-spacing: 0.025em;
  list-style-type: none;
  margin-right: 10px;
}

.about {
  position: absolute;
  z-index: 2;
  bottom: 40px;
  left: 40px;
  display: flex;
  align-items: center;
  font-family: 'Archivo Black', sans-serif;
}

.about h2 {
  margin: 0;
  font-size: 16px;
  letter-spacing: -0.01em;
  margin-right: 20px;
}

.about ul {
  display: flex;
  margin: 0;
  padding: 0;
}

.about ul li {
  list-style-type: none;
  margin-right: 10px;
}

.about ul li a {
  padding: 2px 8px;
  font-size: 12px;
  background: #000;
  color: #fff;
  letter-spacing: 0;
}
